"use client";

import React, { useState, useEffect } from "react";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  CartesianGrid,
  Legend,
} from "recharts";
import Papa from "papaparse";
import { Search, Sparkles, TrendingUp, Zap, Download } from "lucide-react";
import { motion } from "framer-motion";

// Import necessary shadcn components
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";

import { useTheme } from "next-themes"
import { Moon, Sun } from "lucide-react"

interface CSVRow {
  Trend: string;
  "Startup Opportunity": string;
  "Related trends": string;
  "Growth rate, WoW": string;
  "YC chances": string;
  "2025": string;
  "2026": string;
  "2027": string;
  "2028": string;
  "2029": string;
  "2030": string;
}

interface ProcessedRow
  extends Omit<
    CSVRow,
    "2025" | "2026" | "2027" | "2028" | "2029" | "2030"
  > {
  "2025": number;
  "2026": number;
  "2027": number;
  "2028": number;
  "2029": number;
  "2030": number;
}

const years = ["2025", "2026", "2027", "2028", "2029", "2030"];

export default function Home() {
  const [csvData, setCsvData] = useState<ProcessedRow[]>([]);
  const [selectedTrend, setSelectedTrend] = useState<ProcessedRow | null>(null);
  const [searchQuery, setSearchQuery] = useState("");
  const [filteredData, setFilteredData] = useState<ProcessedRow[]>([]);

  const { theme, setTheme } = useTheme();

  useEffect(() => {
    fetch("/Gemini Advanced 20 Pro Experimental.csv")
      .then((res) => res.text())
      .then((csvText) => {
        Papa.parse(csvText, {
          header: true,
          skipEmptyLines: true,
          complete: (results) => {
            const processed: ProcessedRow[] = results.data.map((row: CSVRow) => {
              const newRow = { ...row } as ProcessedRow;
              years.forEach((year) => {
                newRow[year] = parseFloat(newRow[year]?.replace("%", "") || "0");
              });
              return newRow;
            });
            setCsvData(processed);
            setFilteredData(processed);
          },
        });
      });
  }, []);

  useEffect(() => {
    const filtered = csvData.filter((row) =>
      row.Trend.toLowerCase().includes(searchQuery.toLowerCase())
    );
    setFilteredData(filtered);
  }, [searchQuery, csvData]);

  const pivotedData = years.map((year) => {
    const obj: { year: string; [key: string]: number | string } = { year };
    filteredData.forEach((row) => {
      obj[row.Trend] = row[year];
    });
    return obj;
  });

  const colors = [
    "#6366f1", // Indigo
    "#ec4899", // Pink
    "#06b6d4", // Cyan
    "#f59e0b", // Amber
    "#10b981", // Emerald
    "#8b5cf6", // Violet
    "#f43f5e", // Rose
    "#3b82f6", // Blue
    "#84cc16", // Lime
    "#14b8a6", // Teal
  ];

  const getColor = (trend: string, idx: number) => colors[idx % colors.length];

  const handleMouseMove = (state: any) => {
    if (state && state.activePayload && state.activePayload.length > 0) {
      const trendName = state.activePayload[0].dataKey;
      const selected = csvData.find((row) => row.Trend === trendName);
      if (selected) setSelectedTrend(selected);
    }
  };

  const CustomTooltip = ({ active, payload, label }: any) => {
    if (active && payload && payload.length) {
      return (
        <Card className="border-none shadow-lg">
          <CardContent className="p-4">
            <p className="text-sm font-medium mb-2">Year {label}</p>
            <div className="space-y-1">
              {payload.map((entry: any) => (
                <div
                  key={entry.name}
                  className="flex items-center space-x-2"
                >
                  <div
                    className="w-2 h-2 rounded-full"
                    style={{ backgroundColor: entry.color }}
                  />
                  <span className="text-sm font-medium">{entry.name}</span>
                  <span className="text-sm text-gray-500">
                    {entry.value.toFixed(1)}%
                  </span>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      );
    }
    return null;
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-100 dark:from-gray-900 dark:to-gray-800">
      {/* Header */}
      <header className="bg-white/80 dark:bg-gray-800/80 backdrop-blur-lg border-b border-gray-200 dark:border-gray-700 sticky top-0 z-50">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16">
            <div className="flex items-center space-x-2">
              <Sparkles className="h-6 w-6 text-indigo-600 dark:text-indigo-400" />
              <h1 className="text-xl font-semibold text-gray-900 dark:text-white">
                SpyGlass Trends
              </h1>
            </div>
            <div className="flex items-center space-x-4">
              <div className="relative">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                <Input
                  type="text"
                  placeholder="Search trends..."
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  className="pl-10 w-64"
                />
              </div>
              <Button variant="default">
                <Download className="h-4 w-4 mr-2" />
                Export Report
              </Button>
            </div>

              {/* Add this Button component */}            
              <Button
                variant="outline"
                size="icon"
                onClick={() => setTheme(theme === "dark" ? "light" : "dark")}
              >
                <Sun className="h-[1.2rem] w-[1.2rem] rotate-0 scale-100 transition-all dark:-rotate-90 dark:scale-0" />
                <Moon className="absolute h-[1.2rem] w-[1.2rem] rotate-90 scale-0 transition-all dark:rotate-0 dark:scale-100" />
              </Button>
              <Button variant="default">
                <Download className="h-4 w-4 mr-2" />
                Export Report
              </Button>
          </div>
        </div>
      </header>

      {/* Main content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Chart Section */}
          <div className="lg:col-span-2 space-y-8">
            <Card className="overflow-hidden">
              <CardHeader className="space-y-1">
                <div className="flex items-center justify-between">
                  <CardTitle className="text-xl font-semibold">
                    Technology Adoption Trends
                  </CardTitle>
                  <Select defaultValue="5y">
                    <SelectTrigger className="w-32">
                      <SelectValue placeholder="Time Range" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="5y">5 Years</SelectItem>
                      <SelectItem value="3y">3 Years</SelectItem>
                      <SelectItem value="1y">1 Year</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
              </CardHeader>
              <CardContent>
                <div className="h-[600px] w-full">
                  <ResponsiveContainer>
                    <LineChart
                      data={pivotedData}
                      onMouseMove={handleMouseMove}
                      margin={{ top: 20, right: 30, left: 20, bottom: 20 }}
                    >
                      <defs>
                        {colors.map((color, index) => (
                          <linearGradient
                            key={`gradient-${index}`}
                            id={`gradient-${index}`}
                            x1="0"
                            y1="0"
                            x2="0"
                            y2="1"
                          >
                            <stop
                              offset="0%"
                              stopColor={color}
                              stopOpacity={0.4}
                            />
                            <stop
                              offset="100%"
                              stopColor={color}
                              stopOpacity={0}
                            />
                          </linearGradient>
                        ))}
                      </defs>
                      <CartesianGrid
                        strokeDasharray="3 3"
                        stroke="#e5e7eb"
                        opacity={0.4}
                      />
                      <XAxis
                        dataKey="year"
                        stroke="#6b7280"
                        fontSize={12}
                        tickLine={false}
                      />
                      <YAxis
                        stroke="#6b7280"
                        fontSize={12}
                        tickLine={false}
                        domain={[0, 100]}
                        tickFormatter={(value) => `${value}%`}
                      />
                      <Tooltip content={<CustomTooltip />} />
                      {filteredData.map((row, idx) => (
                        <Line
                          key={row.Trend}
                          type="monotone"
                          dataKey={row.Trend}
                          stroke={colors[idx % colors.length]}
                          strokeWidth={2}
                          dot={{
                            r: 4,
                            strokeWidth: 2,
                            fill: "white",
                          }}
                          activeDot={{
                            r: 6,
                            strokeWidth: 2,
                            fill: colors[idx % colors.length],
                          }}
                          fill={`url(#gradient-${idx})`}
                        />
                      ))}
                    </LineChart>
                  </ResponsiveContainer>
                </div>
              </CardContent>
            </Card>

            {/* Trend Cards Grid */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              {filteredData.map((trend, idx) => (
                <motion.div
                  key={trend.Trend}
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ delay: idx * 0.1 }}
                >
                  <Card
                    className={`cursor-pointer transition-all hover:shadow-lg ${
                      selectedTrend?.Trend === trend.Trend
                        ? "ring-2 ring-indigo-500"
                        : ""
                    }`}
                    onClick={() => setSelectedTrend(trend)}
                  >
                    <CardContent className="p-4">
                      <div className="flex items-start justify-between">
                        <div>
                          <h3 className="font-semibold text-gray-900 dark:text-white">
                            {trend.Trend}
                          </h3>
                          <p className="text-sm text-gray-500 mt-1">
                            {trend["Startup Opportunity"]}
                          </p>
                        </div>
                        <Badge
                          variant="secondary"
                          className="flex items-center space-x-1"
                        >
                          <TrendingUp className="h-3 w-3" />
                          <span>{trend["Growth rate, WoW"]}</span>
                        </Badge>
                      </div>
                    </CardContent>
                  </Card>
                </motion.div>
              ))}
            </div>
          </div>

          {/* Details Panel */}
          <div className="lg:col-span-1">
            <Card className="sticky top-24">
              <CardHeader>
                <CardTitle>Trend Details</CardTitle>
              </CardHeader>
              <CardContent>
                {selectedTrend ? (
                  <div className="space-y-6">
                    <div>
                      <h2 className="text-xl font-semibold text-gray-900 dark:text-white">
                        {selectedTrend.Trend}
                      </h2>
                      <p className="mt-2 text-gray-600 dark:text-gray-300">
                        {selectedTrend["Startup Opportunity"]}
                      </p>
                    </div>

                    <div className="grid grid-cols-2 gap-4">
                      <Card>
                        <CardContent className="p-4">
                          <div className="flex items-center space-x-2">
                            <TrendingUp className="h-4 w-4 text-green-500" />
                            <div>
                              <p className="text-sm text-gray-500">Growth Rate</p>
                              <p className="font-semibold">
                                {selectedTrend["Growth rate, WoW"]}
                              </p>
                            </div>
                          </div>
                        </CardContent>
                      </Card>

                      <Card>
                        <CardContent className="p-4">
                          <div className="flex items-center space-x-2">
                            <Zap className="h-4 w-4 text-yellow-500" />
                            <div>
                              <p className="text-sm text-gray-500">YC Chances</p>
                              <p className="font-semibold">
                                {selectedTrend["YC chances"]}
                              </p>
                            </div>
                          </div>
                        </CardContent>
                      </Card>
                    </div>

                    <div>
                      <h3 className="font-medium text-gray-900 dark:text-white mb-2">
                        Related Trends
                      </h3>
                      <div className="flex flex-wrap gap-2">
                        {selectedTrend["Related trends"]
                          .split(",")
                          .map((trend) => (
                            <Badge
                              key={trend.trim()}
                              variant="secondary"
                              className="text-sm"
                            >
                              {trend.trim()}
                            </Badge>
                          ))}
                      </div>
                    </div>
                  </div>
                ) : (
                  <div className="flex items-center justify-center h-full">
                    <p className="text-lg text-gray-500 text-center">
                      Hover over or select a trend line to view detailed information
                    </p>
                  </div>
                )}
              </CardContent>
            </Card>
          </div>
        </div>
      </main>
    </div>
  );
}